.mapContainer {
  border-radius: 5px;
  width: 60%;
  height: 100%;
  @media screen and (max-width: 1000px) {
    width: 100%;
  }
}
.mapContainer > ymap {
  transition: transform 1000ms ease;
}
.popup {
}
.popupPhoto {
  width: 100px;
}
.placemark {
  width: 30px;
  stroke: var(--accent-color);
  margin-right: 10px;
  cursor: pointer;
  z-index: 10000;
}
.flag {
  position: absolute;
  width: 100px;
  height: auto;
  top: 10px;
  left: 10px;
  z-index: 100;
}
